@use 'base/variables' as v;
@use 'base/mixins' as m;

.cursos{
    background-color: v.$azulclaro;
    min-height: 100vh;
    &__contenedor{
        position: relative;
        @include m.contenedor;
        padding: 0rem 0rem 4rem 0rem;
    }

    &__descripciongeneral{
        margin-top: 1rem;
        text-align: center;
        position: relative;
        img{
            width: 16rem;
            @include m.tablet{
                position: absolute;
                width: 18rem;
            }
            @include m.tabletxl{
                width: auto;
            }
        }
        .cursogato{
            top: 0%;
            left: 0%;
            transform: translate(-14%, -15%);
            @include m.tablet{
                width: 22rem;
            }
            @include m.tabletxl{
                transform: translate(-10%, -20%);
            }
        }
        .cursoperro{
            right: 0;
            top: 0;
            transform: translate(10%, -15%);
            @include m.tabletxl{
                transform: translate(10%, -24%);
            }
        }
    }
    &__textdescripcion{
        padding: 1rem 1rem 1rem 2rem;
        background-color: v.$rosa;
        max-width: 108rem;
        width: 92%;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        h3{
            margin: 0;
            font-size: 2.1rem;
            color: v.$amarillo;
            line-height: 1;
            @include m.tablet{
                width: 64%;
            }
            @include m.tabletl{
                font-size: 2.3rem;
            }
            @include m.tabletxl{
                font-size: 2.5rem;
            }

        }
        border-bottom: 8px solid v.$amarillo;
        margin-bottom: 1rem;
        @include m.tablet{
            //margin-bottom: 6rem;
        }
    }

    &__adquerir{
        a{
            @include m.boton(v.$rosa, v.$blanco);
        }
        text-align: center;
    }

    &__modulos{
        .swiper{
            .swiper-wrapper{
                position: relative;
                height: 100%;
                .swiper-slide{
                    height: auto;
                }
            }
        }
    }
    &__modulo{
        height: 100%;
        background-color: rgba(v.$rosaclaro, .8);
        max-width: 40rem;
        width: 95%;
        margin: 0 auto;
        border-radius: 3rem;
        color: v.$grissemiclaro;
    }
    &__heading{
        text-align: center;
        padding-top: 2rem;
    }
    &__lista{
        list-style: disc;
        padding: 1rem 2.4rem 2rem 2.6rem;
        li{
            font-size: 1.3rem;
            font-weight: 250;
            &::marker{  //viñeta de li
                color: v.$verde;
                font-size: 1.5rem;
            }
        }
    }

    &__img3{
        position: absolute;
        right: 0;
        bottom: 0;
        //z-index: 1;
        img{
            width: 18rem;
        }
    }

}