@use 'base/variables' as v;
@use 'base/mixins' as m;

.line-seudoelement{
    &--up{
        &::before{  //creacion de seudo elemento, pinta linea horizontal verde antes del h2 ="cursos-populares" 
            content: ''; 
            width: 4rem;
            height: 0.4rem;
            display: block;
            background-color: v.$verde;
            margin: auto;
            margin-bottom: 1rem;  
        }
    }
    &--down{
        &::after{  //creacion de seudo elemento, pinta linea horizontal verde antes del h2 ="cursos-populares" 
            content: ''; 
            width: 4rem;
            height: 0.4rem;
            display: block;
            background-color: v.$verde;
            margin: auto;
            margin-top: 1rem;  
        }
    }
}

.boton-arriba{
    width: 4rem;
    height: 4rem;
    background-color: v.$gristraparente;
    border-radius: 50%;
    position: fixed;
    bottom: 13rem;
    right: 2.8rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    //display: none;
    visibility: hidden;
    z-index: 8;
    transition: background .5s ease;
    &:hover {
        background-color: v.$gris;
    }
    .btn-up {
        color: v.$blanco;
        font-size: 2rem;
        font-weight: 400;
        padding: 1rem;
    }
}


///////////////barra nav superior y nav movil///////////////////

.barranav{
    //background-color: v.$verdemilitaroscuro;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    @include m.tabletxl{
        width: 50%;
    }

    &__logo{
        width: 15rem;
    }
    &__nav{
        display: none;
        //gap: 1rem;
        a{
            font-size: 1.4rem;
            color: v.$blanco;
            border-left: 1px solid v.$gris;
            padding: .3rem 1rem;
            transition: all .5s ease;
            &:last-of-type{
                border-right: 1px solid v.$gris;
            }
            &:hover{
                color: v.$verde;
                //border-left: 1px solid v.$verde;
            }
        }
        @include m.tabletxl{
            display: flex;
        }
    }
    &__btnmenumovil{
        @include m.tabletxl{
            display: none;
        }
        .bars{
            color: v.$blanco;
            font-size: 3rem;
            cursor: pointer;
        }
    }
}

.sidebarmenumovil{
    position: absolute;
    z-index: 3;
    top: 0%;
    left: 0%;
    width: 0rem;
    height: 100%;
    background-color: rgba(v.$verde,.98);
    overflow: hidden;
    transition: all .6s ease;

    &__navmovil{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 1rem;
        padding: 0 0 0 3rem;
        .logomenumovil{
            width: 12rem;
            margin-bottom: 2rem;
        }
        a{
            font-size: 1.4rem;
            padding: 1rem 0;
            color: v.$grissemiclaro;
            position: relative;
            &::after{
                content: '';
                height: 1px;
                width: 0;
                background-color: v.$blanco;
                display: block;
                position: absolute;
                left: 50%;
                bottom: 0;
                transition: all .5s ease;
            }
            &:hover::after{
                    width: 100%;   
                    left: 0;
            }
            &:hover{
                color: v.$blanco;
            }
        }
    }
}
.menumovil-js{
    width: 20rem;
}