@use 'base/variables' as v;
@use 'base/mixins' as m;

.veterinarias{
    background: linear-gradient(360deg, v.$verdeclaro, v.$verdeextraclaro);
    border-bottom: 4px solid v.$verdemilitaroscuro;

    &__contenedor{
        @include m.contenedor;
        position: relative;
        padding: 7rem 0;
    }
    &__titulo{
        color: v.$verdemilitaroscuro;
    }
    &__sticker{
        position: absolute;
        top: 0;
        right: 0%;
        
        width: 9rem;
        height: 7rem;
        background-color: v.$verdemilitaroscuro;
        border-bottom-left-radius: 1.3rem;
        border-bottom-right-radius: 1.3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        .inyeccion{
            width: 4rem;
        }
    }

    //silder o carrusel de swiper
    &__swiper{
        
    }
    .swiper{
        padding: 4rem 0;
    }
    &__vinculadas{  //swiper-wrapper
        
    }
    .swiper-slide {
    
    }
    &__vinculada{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
    }
    &__miniflag{
        width: 1.5rem;
    }
    &__logo{
        width: 9rem;
        &--agrandar{
            width: 12rem;
        }
    }
    
    .swiper-button-next, .swiper-button-prev{
        
    }
    .swiper-pagination{
          
    }
}