@use 'base/variables' as v;
@use 'base/mixins' as m;

.registromascota{
    min-height: 100vh;
    background-image: linear-gradient(0deg,rgba(v.$verdemilitaroscuro, 1),rgba(v.$verdemilitaroscuro, 0.8)), url(/build/img/BANNER4.jpg);
    background-repeat: no-repeat;
    background-size: cover, cover; 
    background-position: center center, bottom right;
    
    &__contenedor{
        //padding-top: 5rem;
        @include m.contenedor;
    }
    &__formularios{
        padding-top: 3rem;
        color: v.$blanco;
        @include m.tablet{
            display: flex;
            gap: 2rem;
        }
        .formulario{
            @include m.contenedor-sm;
        }
    }
    &__formtitulo{
        padding: 1rem 0;
    }
    &__volver{
        a{
            @include m.boton(v.$verdemilitaroscuro, v.$blanco);
            border: 1px solid darken(v.$grissemiclaro, 20);
        }
        
    }
}